 a su vez, le permite estar más abierto a escuchar a los demás.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Empatía"}},[_c('p',[_vm._v("Otro aspecto clave en la resolución de conflictos es la empatía. Ayudar a los niños a ponerse en el lugar de otra persona no solo les enseña a comprender los sentimientos ajenos, sino que les permite desarrollar relaciones más sanas y respetuosas. Ser capaz de reconocer y respetar las emociones de los demás es una habilidad esencial para vivir en sociedad y resolver cualquier tipo de desacuerdo de manera pacífica.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Negociación"}},[_c('p',[_vm._v("En este proceso, la negociación también juega un papel crucial. Al enseñar a los niños a negociar, les estamos proporcionando herramientas para que aprendan a encontrar soluciones que sean justas para todos. Negociar implica ceder en algunos puntos, comprometerse y ser flexible, habilidades que son esenciales tanto en la infancia como en la vida adulta. Al trabajar juntos para resolver un conflicto, los niños aprenden que no siempre se puede ganar todo, pero sí se puede llegar a acuerdos donde todos se sientan respetados.")])])])],1)]),_vm._m(14),_vm._m(15),_c('div',{staticClass:"bg-full-width border-top actividad bg-color-actividad"},[_c('div',{staticClass:"p-4 p-md-5"},[_c('div',{attrs:{"id":"Actividad"}},[_c('Actividad',{attrs:{"cuestionario":_vm.cuestionario}})],1)])]),_c('div',{staticClass:"bg-full-width border-top color-primario"},[_c('div',{staticClass:"p-4 p-md-5"},[_c('h2',{attrs:{"data-aos":"fade-left"}},[_vm._v("MATERIAL COMPLEMENTARIO")]),_c('div',{staticClass:"row material-complementario"},[_c('div',{staticClass:"col-12 col-md-6 col-lg-7"},[_c('p',[_vm._v("Los invitamos a explorar el material complementario de este curso, en esta sección encontrará recursos que le permitirán profundizar y enriquecer su aprendizaje en los temas tratados en esta unidad.")]),_c('p',{staticClass:"d-flex my-4"},[_c('img',{staticClass:"me-3",st